.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 10px;
    align-items: center;
}

.form__button {
    height: 50px;
    border: none;
    margin-left: 10px;
    background-color: teal;
    color: white;
    padding: 10px;
    margin: 10px;
    transition: 1s;
}

.form__button:hover {
    background-color: hsl(180, 100%, 30%);
    transform: scale(1.1);
}

.form__button:active {
    background-color: hsl(180, 100%, 35%);
}

.form__input {
    height: 50px;
    display: inline-block;
    padding: 10px;
    border-color: lightgray;
    border-style: solid;
}

@media (max-width: 767px) {
    .form {
        display: flex;
        flex-direction: column;
    }

    .form__button {
        width: 100%;
        margin: auto;
    }

    .form__input {
        width: 100%;
    }
}