.section {
    margin: 10px 0;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 5px #ddd;
}

.section__header {
    font-size: 14px;
    padding: 10px;
    margin: 0;
    border-bottom: 1px solid #ddd;
    display: grid;
    grid-template-columns: 1fr auto auto;
    grid-gap: 10px;
    align-items: center;
}

.section__content {
    padding: 20px;
}

@media (max-width: 767px) {
    .section__header {
        display: flex;
        flex-direction: column;
    }

    .section__title {
        width: 100%;
    }
}